.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog__image {
  margin-top: 30px;
  display: block;
  //border: 1px solid $light-grey;
  object-fit: contain;
  box-shadow: inset 0 0 0 1px $light-grey;
  height: 195px;
}

.catalog__link {
  padding-left: 28px;
  padding-bottom: 5px;
  padding-top: 27px;
  text-decoration: none;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.catalog__link_indent {
  padding-bottom: 4px;
  padding-top: 25px;
}

.catalog__link_indent-s {
  padding-bottom: 9px;
  padding-top: 21px;
}

.catalog__descr {
  font-size: 13px;
  line-height: 30px;
  padding: 0 0 20px 28px;
  margin: 0;
  display: block;
}

.catalog__price {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: $black;
  padding: 22px 0 26px;
  display: block;
}

.catalog__decor {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  border-top: 1px solid $light-grey;
  width: 210px;
  margin-left: 28px;
}

.catalog__decor-border {
  display: block;
  border: 1px solid $light-grey;
}

.catalog__button-basket {
  border: none;
  background-color: $white;
  cursor: pointer;
}

@media (min-width: $tablet-width) {
  .catalog__list {
    padding-top: 80px;
  }

  .catalog__item {
    display: flex;
    flex-direction: row;
    margin-bottom: 48px;
  }

  .catalog__image {
    margin: 0;
    height: 334px;
  }

  .catalog__link {
    padding-left: 31px;
  }

  .catalog__decor-border {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .catalog__descr {
    font-size: 15px;
    padding-left: 30px;
  }

  .catalog__link_indent {
    padding-bottom: 10px;
  }

  .catalog__link_indent-s {
    padding-bottom: 7px;
    padding-top: 25px;
  }
}

@media (min-width: $desktop-width) {
  .catalog__list {
    display: grid;
    padding-top: 128px;
    grid-template-columns: repeat(3, auto);
    gap: 51px;
  }

  .catalog__item {
    flex-direction: column;
  }

  .catalog__image {
    height: 462px;
  }

  .catalog__decor-border:hover {
    box-shadow: 0 6px 10px $light-grey;
  }

  .catalog__decor-border:active {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }
}
